import { Field, InputType } from "@nestjs/graphql";
import { Type } from "class-transformer";
import { ArrayMinSize, IsArray, IsNotEmpty, IsOptional, IsString, IsUUID, ValidateNested } from "class-validator";
import { UpdateBrandPaymentGatewayInput } from "./update-brand-payment-gateway-input";
import { UpdateBrandPaymentGatewayPlanInput } from "./update-brand-payment-gateway-plan-input";


@InputType()
export class UpdatePaymentGatewayKeyInput {

  @Field(() => String, { nullable: true })
  @IsNotEmpty()
  @IsOptional()
  @IsUUID(4)
  id: string;

  @Field(() => UpdateBrandPaymentGatewayInput, { nullable: true })
  @ValidateNested()
  @IsOptional()
  @Type(() => UpdateBrandPaymentGatewayInput)
  paymentGateway: UpdateBrandPaymentGatewayInput;

  @Field(() => String, { nullable: true })
  @IsNotEmpty()
  @IsOptional()
  @IsString()
  clientId: string;

  @Field(() => String, { nullable: true })
  @IsNotEmpty()
  @IsOptional()
  @IsString()
  clientSecret: string;

  @Field(() => [UpdateBrandPaymentGatewayPlanInput], { nullable: true })
  @ValidateNested({ each: true })
  @IsArray()
  @ArrayMinSize(1)
  @IsOptional()
  @Type(() => UpdateBrandPaymentGatewayPlanInput)
  paymentGatewayPlans: UpdateBrandPaymentGatewayPlanInput[];


}
